import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';

Vue.config.productionTip = false;

router.beforeEach((to, from, next) => {
  store.commit('setLoading', true);
  // Simulate request
  setTimeout(() => {
    next();
  }, 1500);
});

router.afterEach(() => {
  store.commit('setLoading', false);
});

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');
