import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    loading: false,
  },
  mutations: {
    setLoading(state, bool) {
      // eslint-disable-next-line no-param-reassign
      state.loading = bool;
    },
  },
  getters: {
    getLoading: state => state.loading,
  },
});
