<template>
  <div v-if="mediaQuery !== 'mobile'" class="header-main">
    <div class="group">
      <div class="logo">
        <img src="~@/assets/landing/img-logo-top.svg"/>
      </div>
      <div class="signup">
        <div class="contact-button">
          CONTACT
        </div>
        <div class="signup-button">
          SIGN UP
        </div>
      </div>
    </div>
  </div>
  <div v-else class="header-main">
    <div class="group">
      <div class="logo">
        <img src="~@/assets/landing/img-logo-top.svg"/>
      </div>
      <div class="menu" v-on:click="show()">
        <img src="~@/assets/landing/m-menu.svg"/>
      </div>
    </div>
    <div v-show="isOpen" class="layer-gnb" v-scroll-lock="isOpen">
      <div class="layer-gnb-body">
        <div class="gnb-header">
          <div class="logo">
            <img src="~@/assets/landing/logo-top-blue.svg"/>
          </div>
          <div class="close" v-on:click="close()">
            <img src="~@/assets/landing/m-menuclose.svg"/>
          </div>
        </div>
        <div class="gnb-body">
          <div class="menu">
            MENU01
          </div>
          <div class="menu">
            MENU02
          </div>
          <div class="menu">
            CONTACT
          </div>
        </div>
        <div class="gnb-footer">
          이메일로 쉽게 가입하기
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import VScrollLock from 'v-scroll-lock';

Vue.use(VScrollLock);

export default {
  name: 'Header',
  data() {
    return {
      isOpen: false,
    };
  },
  computed: {
    mediaQuery() {
      return this.$mq;
    },
  },
  methods: {
    show() {
      this.isOpen = true;
    },
    close() {
      this.isOpen = false;
    },
  },
  created() {
    // First we get the viewport height and we multiple it by 1% to get a value for a vh unit
    const vh = window.innerHeight * 0.01;
    // Then we set the value in the --vh custom property to the root of the document
    document.documentElement.style.setProperty('--vh', `${vh}px`);
  },
};
</script>

