<template>
  <div>
    <Header :class="{'headroom--unpinned': scrolled, 'scroll-top': isScrollTop}"
            class="headroom" v-on:scroll="handleScroll" />
    <div v-show="mediaQuery !== 'mobile'" class="main">
      <div class="main-group">
        <div class="left-group">
          <div class="title-header">
            모니터링을 한 곳에서,<br>
            필요한 것만 가볍게,<br>
            그리고 무료로.
          </div>
          <div class="title-desc">
            이제 사용자 분석과 서버 모니터링을 위해 서버용<br>
            에이전트만 설치하면 됩니다. JMSIGHT는 이 모든 것을<br>
            무료로, 한 곳에서 분석할 수 있도록 도와줍니다.
          </div>
          <div class="title-desc-tablet">
            이제 사용자 분석과 서버 모니터링을 위해<br>
            서버용 에이전트만 설치하면 됩니다.<br>
            JMSIGHT는 이 모든 것을 무료로,<br>
            한 곳에서 분석할 수 있도록 도와줍니다.
          </div>
          <div class="signup-button">
            이메일로 쉽게 가입하기
          </div>
        </div>
        <div class="right-group">
          <div id="logo" class="logo-animator"></div>
        </div>
      </div>
    </div>
    <div v-show="mediaQuery === 'mobile'" class="main">
      <div class="main-group">
        <div style="height: calc((100vh - 465px) / 2);"/>
        <div class="logo-wrapper">
          <div id="logo-mobile" class="logo-animator"></div>
        </div>
        <div class="title-header">
          모니터링을 한 곳에서,<br>
          필요한 것만 가볍게,<br>
          그리고 무료로.
        </div>
        <transition name="fade">
          <div v-if="scrollStart || !isScrollTop " class="title-desc">
            이제 사용자 분석과 서버 모니터링을 위해<br>
            서버용 에이전트만 설치하면 됩니다.<br>
            JMSIGHT는 이 모든 것을 무료로,<br>
            한 곳에서 분석할 수 있도록 도와줍니다.
          </div>
        </transition>
        <transition name="fade">
          <div v-if="scrollStart || !isScrollTop" class="signup-button">
            이메일로 쉽게 가입하기
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import Header from './Header.vue';

export default {
  name: 'Main',
  components: {
    Header,
  },
  data() {
    return {
      limitPosition: 100,
      scrolled: false,
      scrollStart: false,
      lastPosition: 0,
      isScrollTop: true,
    };
  },
  methods: {
    handleScroll() {
      if (this.lastPosition < window.scrollY && this.limitPosition < window.scrollY) {
        this.scrolled = true;
        // move up!
      }

      if (this.lastPosition > window.scrollY) {
        this.scrolled = false;
        // move down
      }

      this.lastPosition = window.scrollY;

      if (window.scrollY === 0) this.isScrollTop = true;
      else this.isScrollTop = false;

      this.scrollStart = window.scrollY > 0;
    },
  },
  created() {
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  computed: {
    mediaQuery() {
      return this.$mq;
    },
  },
  watch: {
    // eslint-disable-next-line no-unused-vars
    mediaQuery: (newValue) => {
      const animData = {
        wrapper: document.getElementById('logo'),
        animType: 'html',
        loop: true,
        prerender: true,
        autoplay: true,
        path: 'logo.json',
      };

      const animDataMobile = {
        wrapper: document.getElementById('logo-mobile'),
        animType: 'html',
        loop: true,
        prerender: true,
        autoplay: true,
        path: 'logo.json',
      };
      // eslint-disable-next-line no-undef
      bodymovin.destroy();
      // eslint-disable-next-line no-undef
      bodymovin.loadAnimation(animData);
      // eslint-disable-next-line no-undef
      bodymovin.loadAnimation(animDataMobile);
    },
  },
  mounted() {
    const animData = {
      wrapper: document.getElementById('logo'),
      animType: 'html',
      loop: true,
      prerender: true,
      autoplay: true,
      path: 'logo.json',
    };

    const animDataMobile = {
      wrapper: document.getElementById('logo-mobile'),
      animType: 'html',
      loop: true,
      prerender: true,
      autoplay: true,
      path: 'logo.json',
    };

    // eslint-disable-next-line no-undef
    // bodymovin.destroy();
    // eslint-disable-next-line no-undef
    bodymovin.loadAnimation(animData);
    // eslint-disable-next-line no-undef
    bodymovin.loadAnimation(animDataMobile);
  },
};
</script>

